<template>
    <li class="exam_item" @click="itemClick(examinfo.RoomID)">
        <div class="first">
            <span class="name">{{examinfo.Name}}</span>
            <span class="right status">{{getTimeStr(examinfo.IsFinshed,examinfo.BeginInMinutes)}}</span>
        </div>
        <div class="second">
            <span class="time">
                <span>{{examinfo.StartDate}}</span>
            </span>
            <span class="person_count">
                <span>{{examinfo.Used}}人/{{examinfo.Examed}}人已面</span>
            </span>
        </div>
        <div class="third">
            <span class="item_location">
                <span>{{examinfo.Location}}</span>
            </span>
            <span class="right stage" :stageid='examinfo.PhaseId'>{{examinfo.PhaseName}}</span>
        </div>
    </li>
</template>

<script>
export default {
    methods: {
        getTimeFormat(time) {
            let result = time + '分钟后开始';
            // time/(24*60)>0?time/(24*60)
            let d =Math.floor(time / (24 * 60));
            let h = Math.floor(time / 60);
            result = (d > 0 ? d + '天' : (h > 0 ? h + '小时' : time + '分钟')) + '后开始';
            return result;
        },

        getTimeStr(isFinshed, time, startDate) {
            let result = '';
            if (isFinshed) {
                result = '已结束';
            } else {
                if (time > 0) {
                    result = this.getTimeFormat(time);
                }
                else {
                    result = '进行中'
                }
            }
            return result;

        }
    },
    props: {
        examinfo: { type: Object, default:()=>{return {}} },
        itemClick:{type:Function,default:function(roomID){ this.$router.push({ name: 'ResumeList', query: { roomid: roomID } });}}
    }
}
</script>

<style lang="less" scoped>
@import (reference) "../../assets/css/base.less";
.icon(@w,@h,@x,@y) {
  display: inline-block;
  background: url("../../assets/images/icon.png");
  width: @w;
  height: @h;
  margin-right: 16/@r;
  background-position: @x @y;
  background-size: 640/@r 200/@r;
  vertical-align: middle;
  content: "";
}
.exam_item {
  background: #fff;
  padding: 30/@r;
  color: #909da0;
  font-size: 22/@r;
  margin-top: 16/@r;
  .right {
    float: right;
  }
  .status {
    font-size: 20/@r;
    color: #f29c00;
    line-height: 2;
  }
  .stage{
      font-size: 20/@r;
      line-height: 2;
  }
  .first {
    .name {
      font-size: 28/@r;
      color: #354d5a;
    }
  }
  .second {
    margin-top: 28/@r;
    .time {
      &:before {
        .icon(24/@r,24/@r,-50/@r,-9/@r);
      }
      span {
        vertical-align: middle;
        display: inline-block;
      }
    }
    .person_count {
      margin-left: 16/@r;
      &:before {
        .icon(28/@r,24/@r,-112/@r,-9/@r);
      }
      span {
        vertical-align: middle;
        display: inline-block;
      }
    }
  }
  .third {
    margin-top: 18/@r;
    .item_location {
      &:before {
        .icon(24/@r,28/@r,-82/@r,-9/@r);
      }
      span {
        vertical-align: middle;
        display: inline-block;
      }
    }
  }
}
</style>




